body {
  background-color: #f9f8f8;
  padding-left: 200px;
  padding-right: 200px;
}

.breed-select {
  margin-top: 30px;
  margin-left: 100px;
  padding: 5px;
  display: block;
}

.error {
  display: none;
}

.cat-info {
  width: 500px;
  margin-top: 30px;
}

.loader {
  width: 48px;
  height: 48px;
  color: #fff;
  box-sizing: border-box;
  margin: 15px auto;
  animation: rotation 1s linear infinite;
  display: block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 50%;
  animation: scale50 1s ease-in-out infinite;
  position: absolute;
  top: 0;
}

.loader:before {
  background-color: #ff3d00;
  animation-delay: .5s;
  top: auto;
  bottom: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes scale50 {
  0%, 100% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }
}

.loader {
  display: none;
}

